import React, { useState } from 'react';

function EditableLabel({ inputWidth = 'auto', buttonWidth = '70px' }) {
  // 입력된 데이터와 모드를 관리하기 위한 state 선언
  const [inputValue, setInputValue] = useState('');
  const [isEditing, setIsEditing] = useState(false);

  // 입력 필드 변경을 처리하는 핸들러
  const handleInputChange = event => {
    setInputValue(event.target.value);
  };

  // 모드 전환 (수정/저장)
  const toggleEdit = () => {
    setIsEditing(!isEditing);
  };

  // 입력 필드 초기화 함수
  const handleDelete = () => {
    setInputValue('');
  };

  // 편집 모드에서 저장을 처리하는 함수
  const handleSave = () => {
    if (inputValue.trim()) {
      toggleEdit();
    } else {
      alert('Input cannot be empty.');
    }
  };

  // 편집 모드 취소 함수
  const handleCancel = () => {
    toggleEdit();
  };

  return (
    <div
      style={{
        margin: '20px',
        display: 'flex',
        alignItems: 'center',
        gap: '8px',
      }}
    >
      {isEditing ? (
        <>
          {/* 편집 모드에서는 입력 필드와 '취소', '확인' 버튼을 표시 */}
          <input
            type="text"
            value={inputValue}
            onChange={handleInputChange}
            style={{
              flex: `1 0 ${inputWidth}`,
              padding: '8px',
              borderRadius: '4px',
              border: '1px solid #ccc',
            }}
          />
          <button
            onClick={handleCancel}
            style={{
              width: buttonWidth,
              padding: '8px',
              borderRadius: '4px',
              border: '1px solid #ccc',
              backgroundColor: '#ff4500',
              color: 'white',
              cursor: 'pointer',
            }}
          >
            Cancel
          </button>
          <button
            onClick={handleSave}
            style={{
              width: buttonWidth,
              padding: '8px',
              borderRadius: '4px',
              border: '1px solid #ccc',
              backgroundColor: '#007BFF',
              color: 'white',
              cursor: 'pointer',
            }}
          >
            Save
          </button>
        </>
      ) : (
        <>
          {/* 일반 모드에서는 데이터를 표시하고 '수정'과 '삭제' 버튼을 제공 */}
          <label
            style={{
              flex: `1 0 ${inputWidth}`,
              padding: '8px',
              border: '1px solid #ccc',
              borderRadius: '4px',
              overflow: 'hidden',
              textOverflow: 'ellipsis',
            }}
          >
            {inputValue || 'No data entered'}
          </label>
          <button
            onClick={toggleEdit}
            style={{
              width: buttonWidth,
              padding: '8px',
              borderRadius: '4px',
              border: '1px solid #ccc',
              backgroundColor: '#28a745',
              color: 'white',
              cursor: 'pointer',
            }}
          >
            Edit
          </button>
          <button
            onClick={handleDelete}
            style={{
              width: buttonWidth,
              padding: '8px',
              borderRadius: '4px',
              border: '1px solid #ccc',
              backgroundColor: '#dc3545',
              color: 'white',
              cursor: 'pointer',
            }}
          >
            Delete
          </button>
        </>
      )}
    </div>
  );
}

export default EditableLabel;
