import { includes } from 'ramda';

/**
 * id, label, value, onClick, isChecked 를 받아서 Checkbox 를 만들어준다.
 * isChecked 를 사용자가 주입해준다. 즉 외부에서 결정한다.
 * @param { id, label, value, onClick, isChecked, children } param0
 * @returns JSX.Element
 */
export const Checkbox = ({
  id,
  label,
  value,
  onClick,
  isChecked,
  children,
}) => {
  const handleClick = e => {
    const newIsChecked = !isChecked;
    onClick(newIsChecked, id, value);
  };

  return (
    <div>
      <input type="checkbox" checked={isChecked} onChange={handleClick} />{' '}
      {label}
      {children}
    </div>
  );
};

/**
 * items 와 itemsChecked 를 받아서 CheckList 를 만들어준다.
 * itemsChecked 는 items 중에서 선택된 것들을 배열로 가지고 있다.
 * onCheck 는 itemsChecked 를 paremter 에서 받는 함수이다. 내부의 Checkbox 가 클릭시에 이 함수를 호출한다.
 * items 의 item 은 { id, label, value } 의 형태로 일반화 하였다.
 * itemsChecked 의 id 를 비교하여 해당 item 이 선택되었는지 확인하고 Checkbox 의 isChecked 를 결정한다.
 * @param { items: { id, label, value }[], onCheck: (newItemsChecked) => {}, itemsChecked } param
 * @returns JSX.Element
 */
export const CheckListType1 = ({ items, onCheck, itemsChecked }) => {
  /**
   * Checkbox 가 클릭되었을 때 호출되는 함수
   * 클릭된 item 을 itemsChecked 에 추가하거나 제거한다.
   * @param {*} isChecked
   * @param {*} itemChecked
   */
  const handleOnToggleClick = (isChecked, itemChecked) => {
    const newItemsChecked = isChecked
      ? [...itemsChecked, itemChecked]
      : itemsChecked.filter(el => el.id !== itemChecked.id);
    onCheck(newItemsChecked);
  };

  return (
    <>
      {items.map(item => (
        <Checkbox
          item={item}
          label={item.label}
          key={item.id}
          isChecked={includes(
            item.id,
            itemsChecked.map(item => item.id),
          )}
          onClick={isChecked => handleOnToggleClick(isChecked, item)}
        />
      ))}
    </>
  );
};
